import { Plugin, ViteDevServer } from 'vite';

export default function devSecretsPlugin(secretNames: Array<string>): Plugin {
  return {
    name: 'dev-secrets',
    configureServer: {
      order: 'post',
      async handler(server: ViteDevServer) {
        const cmsComponents = await server.ssrLoadModule(
          '@hubspot/cms-components'
        );

        if (cmsComponents.setSecretsFromEnv) {
          console.log(
            `Attempting to load HS_* secrets from .env: ${secretNames}`
          );
          cmsComponents.setSecretsFromEnv(secretNames);
        } else if (secretNames.length > 0) {
          console.warn(
            'Not loading secrets into the local environment. Make sure that your @hubspot/cms-components and @hubspot/cms-dev-server package versions match.'
          );
        }
      },
    },
  };
}
