/* eslint-disable require-await */

import { createServer as createViteServer, InlineConfig } from 'vite';
import { default as react } from '@vitejs/plugin-react';
import {
  Gate,
  clientIslandEntryPlugin,
  resolveIslandPlugin,
  noncePlaceholderPlugin,
  findCmsModules,
} from '@hubspot/cms-js-internal';
import { default as mkcert } from 'vite-plugin-mkcert';
import { viteCommonjs } from '@originjs/vite-plugin-commonjs';
import devSecretsPlugin from './plugins/devSecretsPlugin.js';

async function createViteDevServer(
  webRoot: string,
  port,
  sslEnabled: boolean,
  domainsToProxy = [],
  jsRenderingGates: Gate[],
  secretNames: string[]
) {
  const additionalMkcertHosts = domainsToProxy
    .map((proxyDomain) => [
      `${proxyDomain}.localhost`,
      `${proxyDomain}.hslocal.net`,
    ])
    .flat();

  if (process.env.VITE_ORIGIN) {
    console.log(`\nCustom origin: ${process.env.VITE_ORIGIN}\n`);
  }

  const origin =
    process.env.VITE_ORIGIN ??
    (sslEnabled ? `https://hslocal.net:${port}` : `http://hslocal.net:${port}`);

  const viteConfig: InlineConfig = {
    appType: 'custom',
    server: {
      middlewareMode: true,
      host: 'hslocal.net',
      origin,
      https: sslEnabled,
      hmr: {
        protocol: sslEnabled ? 'wss' : undefined,
      },
    },
    root: webRoot,
    ssr: {
      // TODO: might need more entries here to get things to resolve through
      // resolveWebRootDepsPlugin
      noExternal: '@hubspot/cms-components',
    },
    resolve: {
      conditions: ['server'],
      dedupe: ['react', 'react-dom', '@hubspot/cms-components', 'styled-jsx'],
    },
    optimizeDeps: {
      include: ['react', 'react-dom', 'react-dom/client'],
    },
    plugins: [
      true ? findCmsModules() : null,
      sslEnabled ? mkcert({ hosts: additionalMkcertHosts }) : null,
      react({
        babel: {
          plugins: [
            'babel-plugin-styled-components',
            'styled-jsx/babel',
            'macros',
          ],
        },
      }),
      clientIslandEntryPlugin(),
      resolveIslandPlugin(),
      jsRenderingGates.includes('CMS:JSRenderer:RemoveCjsPlugin')
        ? null
        : viteCommonjs(),
      devSecretsPlugin(secretNames),
      noncePlaceholderPlugin(),
    ],
    css: {
      postcss: webRoot,
    },
    envDir: webRoot,
    envPrefix: 'HS_',
  };

  return createViteServer(viteConfig);
}

export default createViteDevServer;
